import React from 'react';
import './BlackScreenButton.css';

function BlackScreenButton({ iconImg, url, altText = 'button', onButton }) {
  if (url && !onButton) {
    return (
      <a className="black-screen-btn flex-center" href={url} target="_blank">
        {iconImg && <img src={iconImg} alt={altText} />}
      </a>
    );
  }

  return (
    <div className="black-screen-btn flex-center" onClick={onButton}>
      <img src={iconImg} alt={altText} />
    </div>
  );
}

export default BlackScreenButton;
