import { useState } from 'react';
import { Route, Routes } from 'react-router-dom';
import useLocalStorage from 'use-local-storage';
import { ProfileProvider } from 'context/profileState';
import { ChatDataProvider } from 'context/chatDataState';
import { SelectionDataProvider } from 'context/selectionDataState';
import { RestartProvider } from 'context/restartState';
import NotFound from 'pages/NotFound';
import HomePage from 'pages/HomePage/HomePage';
import CircularButton from 'components/CircularButton/CircularButton';
import Settings from 'components/Settings/Settings';
import './App.css';

function App() {
  const [showSettings, setShowSettings] = useState(false);

  const defaultColor = window.matchMedia(
    '(prefers-color-scheme: colorful)'
  ).matches;
  const [theme, setTheme] = useLocalStorage(
    'theme',
    defaultColor ? 'colorful' : 'dark'
  );

  const switchTheme = () => {
    const newTheme = theme === 'colorful' ? 'dark' : 'colorful';
    setTheme(newTheme);
  };

  const onShowSettings = (e) => {
    // Prevent bubbling
    e.stopPropagation();
    setShowSettings(true);
  };

  const onHideSettings = () => {
    setShowSettings(false);
  };

  return (
    <ProfileProvider>
      <ChatDataProvider>
        <SelectionDataProvider>
          <RestartProvider>
            <div className="App-bg" data-theme={theme} onClick={onHideSettings}>
              <CircularButton
                iconImg="/settings.png"
                altText="Settings button"
                onFunction={onShowSettings}
              />
              <Settings
                showSettings={showSettings}
                theme={theme}
                onToggle={switchTheme}
              />
              <div className="App">
                <Routes>
                  <Route exact path="/" element={<HomePage />} />
                  <Route component={NotFound} />
                </Routes>
              </div>
            </div>
          </RestartProvider>
        </SelectionDataProvider>
      </ChatDataProvider>
    </ProfileProvider>
  );
}

export default App;
