import React, { useEffect, useState } from 'react';
import { useRestartContext } from 'context/restartState';
import ToggleSwitch from 'components/ToggleSwitch/ToggleSwitch';
import './Settings.css';

const SettingsMenu = ({
  iconImg,
  url,
  altText = 'menu button',
  onSettingsMenu,
  menuText
}) => {
  if (url && !onSettingsMenu) {
    return (
      <a className="settings-menu" href={url} target="_blank" rel="noreferrer">
        <img src={iconImg} alt={altText} />
        <p>{menuText}</p>
      </a>
    );
  }

  return (
    <div className="settings-menu" onClick={onSettingsMenu}>
      <img src={iconImg} alt={altText} />
      <p>{menuText}</p>
    </div>
  );
};

function Settings({ showSettings, theme, onToggle }) {
  const { onRestart } = useRestartContext();
  const [ShowAnimation, setShowAnimation] = useState(false);

  const externalUrl = 'https://lovebyte.cargo.site/';

  const onSettings = (e) => {
    // Prevent bubbling
    e.stopPropagation();
  };

  useEffect(() => {
    if (showSettings) {
      setShowAnimation(false);
    } else {
      const timerId = setTimeout(() => setShowAnimation(true), 1200);
      return () => clearTimeout(timerId);
    }
  }, [showSettings]);

  return (
    <div
      onClick={onSettings}
      className={`settings ${
        showSettings ? 'show-settings' : 'hide-settings'
      } ${ShowAnimation && 'display-hidden'}`}
    >
      <ToggleSwitch label="Color Theme" theme={theme} onToggle={onToggle} />
      <SettingsMenu
        iconImg="/restart.png"
        alt="restart chat"
        onSettingsMenu={onRestart}
        menuText="Restart Chat"
      />
      <SettingsMenu
        iconImg="/home.png"
        alt="open homepage"
        menuText="Open Website"
        url={externalUrl}
      />
    </div>
  );
}

export default Settings;
