import React, { useState } from 'react';
import { useProfileContext } from 'context/profileState';
import { Chat } from 'components/Chats/Chats';
import './GoogleForm.css';

const CheckBox = ({ checkBoxId, nickname, profileImg, isChecked, onCheck }) => (
  <div
    className={`google-checkbox ${isChecked && 'is-checked'} flex-center`}
    onClick={() => onCheck(checkBoxId)}
  >
    <Chat profileImg={profileImg} text={nickname} isUser={false} />
  </div>
);

function CheckBoxInput({ field, onCheck }) {
  const { profileList } = useProfileContext();

  const [CheckedOne, setCheckedOne] = useState(false);
  const [CheckedTwo, setCheckedTwo] = useState(false);

  const onCheckBox = (checkBoxId) => {
    if (checkBoxId === 1) {
      setCheckedOne(true);
      setCheckedTwo(false);
    } else {
      setCheckedOne(false);
      setCheckedTwo(true);
    }
    onCheck(checkBoxId);
  };

  return (
    <div>
      <h1>{field.label}</h1>
      {profileList && profileList.length === 2 && (
        <div className="google-checkbox-container select-none">
          <CheckBox
            checkBoxId={1}
            nickname={profileList[0].nickname}
            profileImg={profileList[0].profileImg}
            isChecked={CheckedOne}
            onCheck={onCheckBox}
          />
          <CheckBox
            checkBoxId={2}
            nickname={profileList[1].nickname}
            profileImg={profileList[1].profileImg}
            isChecked={CheckedTwo}
            onCheck={onCheckBox}
          />
        </div>
      )}
    </div>
  );
}

export default CheckBoxInput;
