import React, { useEffect, useState } from 'react';
import { useProfileContext } from 'context/profileState';
import { useChatDataContext } from 'context/chatDataState';
import { useSelectionDataContext } from 'context/selectionDataState';
import Phone from 'components/Phone/Phone';
import Chats from 'components/Chats/Chats';
import Selections from 'components/Selections/Selections';
import LoadingBar from 'components/LoadingBar/LoadingBar';
import BlackScreen from 'components/BlackScreen/BlackScreen';
import ChoosePerson from 'components/ChoosePerson/ChoosePerson';
import './HomePage.css';

function HomePage() {
  const { profileList, chatProfile } = useProfileContext();
  const { chatData } = useChatDataContext();
  const { selectionData } = useSelectionDataContext();

  const [loadOnce, setLoadOnce] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [currStep, setCurrStep] = useState(0);

  const onNextStep = () => {
    setLoadOnce(false);
    setIsLoading(true);
  };

  useEffect(() => {
    // Load profile
    if (!isLoading) return;
    if (!profileList || profileList.length === 0) return;
    setLoadOnce(true);
    const timerId = setTimeout(() => {
      setIsLoading(false);
      setCurrStep((c) => c + 1);
    }, 4000);
    return () => clearTimeout(timerId);
  }, [profileList, isLoading]);

  useEffect(() => {
    // Load chat data and selection data
    if (!isLoading) return;
    if (!chatProfile?.index) return;
    if (!chatData || chatData.length === 0) return;
    if (!selectionData || selectionData.length === 0) return;
    setLoadOnce(true);
    const timerId = setTimeout(() => {
      setIsLoading(false);
      setCurrStep((c) => c + 1);
    }, 4000);
    return () => clearTimeout(timerId);
  }, [chatProfile, chatData, selectionData, isLoading]);

  if (isLoading) {
    return (
      <div className="home">
        <div className="flex-col">
          <LoadingBar
            isLoading={isLoading}
            loadOnce={loadOnce}
            loadingText={
              currStep === 0
                ? 'Searching for your match...'
                : 'Connecting to chat...'
            }
          />
        </div>
      </div>
    );
  }

  return (
    <div className="home">
      <BlackScreen />
      {currStep === 1 && <ChoosePerson onNextStep={onNextStep} />}
      {currStep === 2 && (
        <>
          <Phone>
            <Chats />
          </Phone>
          <Selections />
        </>
      )}
    </div>
  );
}

export default HomePage;
