import React from 'react';
import './CircularButton.css';

function CircularButton({ iconImg, altText = 'button', onFunction }) {
  const onButton = (e) => {
    onFunction(e);
  };

  return (
    <div className="circle-btn flex-center" onClick={onButton}>
      <img src={iconImg} alt={altText} />
    </div>
  );
}

export default CircularButton;
