import { useEffect, useRef, useState } from 'react';
import { useProfileContext } from 'context/profileState';
import { useChatDataContext } from 'context/chatDataState';
import { useSelectionDataContext } from 'context/selectionDataState';
import Profile from 'components/Profile/Profile';
import EmptyList from 'components/EmptyList/EmptyList';
import './Chats.css';

export function Chat({ profileImg, text, isUser, count, needsLoad = false }) {
  const [isChatLoading, setIsChatLoading] = useState(true);
  const [isChatAppear, setIsChatAppear] = useState(null);

  useEffect(() => {
    // Chat typing animation delay controller
    if (!needsLoad || isUser || count === 0) {
      setIsChatLoading(false);
      setIsChatAppear(true);
      return;
    }
    if ((isChatLoading === false && isChatAppear === true) || !count) return;
    if (count === 1) {
      setIsChatAppear(true);
      const timerId = setTimeout(() => {
        setIsChatLoading(false);
      }, 2000);
      return () => clearTimeout(timerId);
    }
    if (isChatAppear) {
      const timerId = setTimeout(() => {
        setIsChatLoading(false);
        setIsChatAppear(true);
      }, 2000);
      return () => clearTimeout(timerId);
    }
    if (count > 1) {
      setIsChatAppear(false);
      const timerId = setTimeout(
        () => {
          setIsChatAppear(true);
        },
        (count - 1) * 2000
      );
      return () => clearTimeout(timerId);
    }
  }, [needsLoad, isUser, count, isChatAppear, isChatLoading]);

  return (
    <div className={`chat flex-row ${isUser && 'user'}`}>
      {isChatAppear && !isUser && <Profile profileImg={profileImg} />}
      {isChatAppear && (
        <div className={`bubble ${isUser && 'user'}`}>
          {isChatLoading ? (
            <div className="spinner">
              <div className="bounce1"></div>
              <div className="bounce2"></div>
              <div className="bounce3"></div>
            </div>
          ) : (
            <p>{text}</p>
          )}
        </div>
      )}
    </div>
  );
}

function Chats() {
  const chatRef = useRef(null);

  const { chatProfile } = useProfileContext();
  const { chatData, chatList, setChatList } = useChatDataContext();
  const { setSelectionId, selectedPath, onClean } = useSelectionDataContext();

  const [isFetching, setIsFetching] = useState(true);
  const [Chats, setChats] = useState([]);

  useEffect(() => {
    // Double check if chat data are there
    setIsFetching(true);
    if (!chatProfile?.index) return;
    if (chatData && chatData.length > 0) {
      setIsFetching(false);
    }
  }, [chatProfile, chatData, isFetching]);

  const searchChatDataById = (chatId) => {
    const search = chatData.filter((s) => {
      return s.chatId === chatId;
    });
    if (search && search[0]) return search[0];
    return null;
  };

  const loadChatsUntilSelection = () => {
    let chats = !chatList || chatList.length === 0 ? [chatData[0]] : chatList;
    let count = 0;
    if (selectedPath) {
      chats.push({
        chatId: null,
        isUser: '1',
        nextChat: selectedPath.nextChat,
        nextScenario: '',
        text: selectedPath.selectedText
      });
      const nextChat = searchChatDataById(selectedPath.nextChat);
      if (nextChat) {
        count++;
        chats.push({ ...nextChat, timeCount: count });
      }
      onClean();
    }
    let isSelection = false;
    while (!isSelection) {
      const latestChat = chats.slice(-1)[0];
      if (latestChat?.nextScenario) {
        // Now selection
        setSelectionId(latestChat.nextScenario);
        isSelection = true;
      } else if (latestChat?.nextChat) {
        // Keep chat
        const nextChat = searchChatDataById(latestChat.nextChat);
        if (nextChat) {
          count++;
          chats.push({ ...nextChat, timeCount: count });
        } else {
          isSelection = true;
        }
      } else {
        // Chat ended
        isSelection = true;
        onClean();
      }
    }
    setChatList(chats);
    setChats(chats);
  };

  useEffect(() => {
    if (isFetching) return;
    if (!chatData || chatData.length === 0) {
      setChatList([]);
      setChats([]);
    } else {
      loadChatsUntilSelection();
    }
  }, [isFetching, chatData, chatList, selectedPath, setChatList]);

  useEffect(() => {
    // Scroll to bottom
    chatRef.current?.scrollIntoView({ behavior: 'smooth' });
  });

  const chatBubble =
    Chats && Chats.length > 0 ? (
      Chats.map((chat, index) => (
        <Chat
          key={`chat-${index}`}
          text={chat.text}
          profileImg={chatProfile.profileImg}
          isUser={chat.isUser === '1'}
          count={Chats.length === 1 ? 0 : chat.timeCount}
          needsLoad={true}
        />
      ))
    ) : (
      <EmptyList />
    );

  return (
    <div className="chats">
      {chatBubble}
      <div ref={chatRef} />
    </div>
  );
}

export default Chats;
