import React from 'react';
import { useProfileContext } from 'context/profileState';
import './Phone.css';

function Phone({ children }) {
  const { chatProfile } = useProfileContext();

  return (
    <div className="mobile">
      <div className="mobile-top">
        {chatProfile?.profileImg && (
          <img src={chatProfile.profileImg} alt="header profile image" />
        )}
        {chatProfile?.nickname && chatProfile.nickname}
      </div>
      {children}
    </div>
  );
}

export default Phone;
