/* Toggle Switch source: https://www.geeksforgeeks.org/how-to-create-a-toggle-switch-in-react-as-a-reusable-component/ */

import React from 'react';
import './ToggleSwitch.css';

function ToggleSwitch({ label, theme, onToggle }) {
  const onTogglePreventBubbling = (e) => {
    e.stopPropagation();
  };

  return (
    <div className="toggle-container">
      {/* <h4>{label}</h4> */}
      <div className="toggle">
        <input
          type="checkbox"
          className="toggle-checkbox"
          name={label}
          id={label}
          value={theme}
          onClick={onTogglePreventBubbling}
          onChange={onToggle}
        />
        <label className="toggle-label" htmlFor={label}>
          <span className="toggle-inner" />
          <span className="toggle-switch" />
        </label>
      </div>
    </div>
  );
}

export default ToggleSwitch;
