import './LoadingBar.css';

function LoadingBar({ isLoading = true, loadOnce = false, loadingText = '' }) {
  return (
    <div
      className={`bar-container ${
        loadOnce ? 'loading-once' : isLoading ? 'loading' : ''
      }`}
    >
      <img src="/logo512.png" alt="" />
      <div className="loading-bar-base"></div>
      <div className="loading-bar"></div>
      <h2>{loadingText}</h2>
    </div>
  );
}

export default LoadingBar;
