import React, { useEffect, useState } from 'react';
import { useProfileContext } from 'context/profileState';
import { useRestartContext } from 'context/restartState';
import GoogleForm from 'components/GoogleForm/GoogleForm';
import BlackScreenButton from 'components/BlackScreenButton/BlackScreenButton';
import './BlackScreen.css';

function BlackScreen() {
  const { profileList, chatProfile, setChatProfile } = useProfileContext();
  const { restart, onRestart, onNewChat } = useRestartContext();

  const [hasSubmitted, setHasSubmitted] = useState(false);
  const [isSkipped, setIsSkipped] = useState(false);
  const [NextProfile, setNextProfile] = useState(null);

  const externalUrl = 'https://lovebyte.cargo.site/';

  useEffect(() => {
    if (!profileList || !chatProfile?.index) return;
    const index = chatProfile.index === 1 ? 2 : 1;
    setNextProfile({ index, ...profileList[index - 1] });
  }, [profileList, chatProfile]);

  const onGoogleForm = (hasVoted) => {
    setIsSkipped(false);
    setHasSubmitted(hasVoted);
  };

  const onResetGoogleForm = () => {
    setIsSkipped(false);
    setHasSubmitted(false);
  };

  const onProfileButton = () => {
    onResetGoogleForm();
    setChatProfile(NextProfile);
    onNewChat();
  };

  if (!restart) return null;
  return (
    <div className="black-screen flex-center">
      {isSkipped || hasSubmitted ? (
        <div className="black-buttons">
          <BlackScreenButton
            iconImg="/restart.png"
            alt="restart chat"
            onButton={onRestart}
          />
          {NextProfile && (
            <BlackScreenButton
              iconImg={NextProfile.profileImg}
              alt="chat with the other person"
              onButton={onProfileButton}
            />
          )}
          {!hasSubmitted && (
            <BlackScreenButton
              iconImg="/form.png"
              alt="submit google form"
              onButton={() => onGoogleForm(false)}
            />
          )}
          <BlackScreenButton
            iconImg="/home.png"
            alt="move to homepage"
            url={externalUrl}
          />
        </div>
      ) : (
        <div className="google-form">
          <GoogleForm onGoogleForm={onGoogleForm} />
          <button type="button" onClick={() => setIsSkipped(true)}>
            Skip the form
          </button>
        </div>
      )}
    </div>
  );
}

export default BlackScreen;
