import React, { useState } from 'react';
import { GoogleFormProvider, useGoogleForm } from 'react-google-forms-hooks';
import form from 'assets/form';
import CheckBoxInput from './CheckBoxInput';
import LongAnswerInput from './LongAnswerInput';
import './GoogleForm.css';

function GoogleForm({ onGoogleForm }) {
  const methods = useGoogleForm({ form });

  const [CheckBox, setCheckBox] = useState(null);
  const [Text, setText] = useState('');

  const onCheck = (checked) => {
    setCheckBox(checked);
  };

  const onText = (event) => {
    setText(event.target.value);
  };

  const onSubmit = async () => {
    if (CheckBox === null) {
      alert('You should check the person before submit.');
      return;
    }
    const voteResult = CheckBox === 1 ? 'AI' : 'Real Person';
    const googleFormJson = {
      [form.fields[0].id]: voteResult,
      [form.fields[1].id]: Text
    };
    await methods.submitToGoogleForms(googleFormJson);
    alert('Form submitted with success!');
    onGoogleForm(true);
  };

  return (
    <GoogleFormProvider>
      <form
        className="google-form-container"
        onSubmit={methods.handleSubmit(onSubmit)}
      >
        <CheckBoxInput field={form.fields[0]} onCheck={onCheck} />
        <LongAnswerInput field={form.fields[1]} text={Text} onText={onText} />
        <button type="submit">Submit</button>
      </form>
    </GoogleFormProvider>
  );
}

export default GoogleForm;
